//*   -, -       :
function createInfoMovieMarkup(infoFilm) {
    // console.log("createInfoMovieMarkup ==> infoFilm:", infoFilm); //!
    const {
        id,
        poster_path,
        title, name,
        vote_average,
        vote_count,
        popularity,
        original_title,
        original_name,
        genres,
        overview
    } = infoFilm

    //?          
    // console.log("poster_path:", poster_path); //!
    let posterImage = "https://image.tmdb.org/t/p/w300" + poster_path
    // console.log("posterImage:", posterImage); //!
    // console.log(typeof (posterImage)); //!  ,   - 

    //?  poster_path  null
    if (!poster_path) posterImage = defaultImageModal;

    //?     
    const genresAllOneFilm = genres.map(item => item.name).join(", ");

    //?       
    let capitalsTitle = title;
    if (title) {
        capitalsTitle = title.toUpperCase();
        // const title = title.toUpperCase();
        // console.log("capitalsTitle:", capitalsTitle); //!
    };

    let capitalsName = name;
    if (name) {
        const capitalsName = name.toUpperCase(); //!!!    !!!
        // const name = name.toUpperCase();
        // console.log("capitalsName:", capitalsName); //!
    };

    //?      MY LIBRARY
    const currentPageMyLibrary = "Your current Library is " + currentPage.toUpperCase()
    // console.log("currentPageMyLibrary:", currentPageMyLibrary); //!

    return `
        <div class="modal-markup">
            
            <img src="${posterImage}" alt="${title || name}" />
            
            <div class="modal-ontent">
                <h4 class="modal-memo-Library">${(currentPage === "watched" || currentPage === "queue") ? currentPageMyLibrary : ''}</h4>
                <h3>${capitalsTitle || capitalsName}</h3>
                <h5>Vote/Votes: &nbsp${vote_average}/${vote_count}</h5>
                <h5>Popularity: &nbsp${popularity}</h5>
                <h5>Original Title: &nbsp${original_title || original_name}</h5>
                <h5>Genre: &nbsp${genres.length !== 0 ? genresAllOneFilm : 'No genres...'}</h5>
                <h5>ABOUT</h5>
                <p>${overview}</p>
            </div>

        </div>

        <!--! -Markup <ADD TO WATCHED>  <ADD TO QUEUE> -->
        <div class="modal-library">
            <button 
                type="button" 
                class="modal-watched"
                data-action="modal-add-watched"
                >
                ADD TO WATCHED
            </button>
            <button 
                type="button" 
                class="modal-queue"
                data-action="modal-add-queue"
                >
                ADD TO QUEUE
            </button>
        </div>
    `;
};


